body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.Buttons{margin:auto;width:50vw;background-color:#3f3f3f;border-radius:10px}button{margin:1vw;margin-top:1vw;width:8vw;height:3.5vw;font-size:2.25vw;font-weight:bold;border-radius:15px;background-color:#868686;color:white;outline:none}#pink{background-color:#ad2a3c}#result{background-color:#38abd1}#on{background-color:#419445}.number{background-color:#b3b3b3}p{font-size:0.5vw}@media screen and (max-width: 600px){.Buttons{width:95vw;margin-top:25vw}button{margin:2vw;width:15vw;height:10vw;font-size:6vw;margin-top:5vw}}

.Calculator{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;margin:auto;margin-top:1vh;padding-top:4vh;width:52vw;padding-bottom:2vh;border:1px solid black;border-radius:15px;background-color:#242424;box-shadow:1px 1px 0px #999, 2px 2px 0px #999, 3px 3px 0px #999, 4px 4px 0px #999, 5px 5px 0px #999, 6px 6px 0px #999, 7px 7px 0px #999, 8px 8px 0px #999, 9px 9px 0px #999, 10px 10px 0px #999}.Calculator h1{text-align:left;color:white;margin-left:1vw;font-style:italic}.sorry{-webkit-animation-name:animation;animation-name:animation;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-fill-mode:inherit;animation-fill-mode:inherit;-webkit-animation-fill-mode:backwards;animation-fill-mode:backwards;-webkit-animation-iteration-count:infinite;animation-iteration-count:infinite}@-webkit-keyframes animation{25%{margin-top:-5vw}75%{margin-top:5vw}}@keyframes animation{25%{margin-top:-5vw}75%{margin-top:5vw}}@media screen and (max-width: 600px){.Calculator{width:100vw;height:100vh;margin-top:0;border-radius:0;box-shadow:none}}

@font-face{font-family:"digital-clock-font";src:url(/react-calculator/static/media/digital-7.regular.1e670d88.ttf)}.Output{vertical-align:middle;line-height:10vh;overflow:hidden;margin:auto;height:9vh;margin-bottom:5vh;width:50vw;text-align:right;padding-right:1vw;background-color:#517c64;font-size:10vh;font-family:digital-clock-font;border-radius:5px}.twelve{font-size:7.9vh}.fifteen{font-size:5.25vh}@media screen and (max-width: 600px){.Output{width:95vw}}


